import React, { Component } from 'react';

class Manual extends Component {
  render() {
    return (
      <div className="main-content">
        <div className="manual">
          <div className="manual__section manual__section--context">
            <h2 className="manual__title">Contexto</h2>
            <ul>
              <li>El juego generar un nmero al azar</li>
              <li>Ese nmero tendr una cantidad determinada de dgitos</li>
              <li>La cantidad de dgitos que tenga tal nmero depende del nivel que escojas</li>
              <li>Debes adivinar todos los dgitos que el juego gener</li>
              <li>Pon a prueba tu lgica</li>
            </ul>
          </div>

          <div className="manual__section manual__section--rules">
            <h2 className="manual__title">Reglas</h2>
            <ul>
              <li>No puedes repetir dgitos</li>
              <li>No te preocupes, el juego tampoco generar un nmero que tenga dgitos repetidos</li>
              <li>El juego te dir la cantidad de dgitos que adivinaste</li>
              <li>El orden en que coloques los dgitos no importa. Solo debes adivinarlos  (excepto en nivel Lotera)
              </li>
              <li>Cada vez que reinicies el juego, se generar un nuevo nmero</li>
              <li>
                Mientras menos intentos necesites para ganar, estars mejor ubicado en la tabla de posiciones (comparado con otros jugadores)
              </li>
            </ul>

            <h3 className="manual__title">Nivel Lotera</h3>
            <ul>
              <li>En este nivel s importa el orden en que coloques los dgitos</li>
              <li>El juego te dir la cantidad de colocaciones que adivinaste</li>
              <li>El resto de reglas sigue aplicando</li>
            </ul>

          </div>

          <div className="manual__section manual__section--levels">
            <h2 className="manual__title">Niveles</h2>
            <p><span className="manual__subtitle">Fcil: </span> 4 dgitos a adivinar</p>

            <p><span className="manual__subtitle">Moderado: </span> 5 dgitos a adivinar</p>

            <p><span className="manual__subtitle">Difcil: </span> 6 dgitos a adivinar</p>

            <p><span className="manual__subtitle">Lotera: </span> 5 dgitos a adivinar</p>

          </div>

          <div className="manual__section manual__section--examples">
            <h2 className="manual__title">Ejemplo</h2>
              <ul>
                <li>Escogiste nivel moderado</li>
                <li>El juego genera un nmero al azar con 5 dgitos (<span className="guessed">8 1 0 4</span> 9)</li>
                <li>T enviaste el siguiente nmero: 7 <span className="guessed">0 8 1 4</span></li>
                <li>El juego te dice que adivinaste 4 dgitos (0, 8, 1, 4)</li>
                <li>Cuando adivines todos los dgitos, ganas (<span className="guessed">9 4 0 1 8</span>)</li>
              </ul>
              <h3 className="manual__title">Como puedes ver...</h3>
              <ul>
                <li>El orden no importa</li>
                <h3 className="manual__title">Pero...</h3>
                <li>Si hubieras elegido nivel Lotera, aparte de adivinar todos los dgitos, tambin tienes que adivinar el orden en que el juego los gener</li>
                <li>En pocas palabras, para ganar, tienes que colocar el nmero tal cual como fue generado (<span className="guessed">8 1 0 4 9</span>)</li>

              </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default Manual;


